<!--
 * @Description: 提现订单列表
 * @LastEditors: Ji Yinghao
 * @Date: 2019-04-29 15:19:29
 * @LastEditTime: 2019-10-09 18:22:42
 -->

<template>
  <div>
    <!-- 导航 -->
    <Breadcrumbs :items="items" />
    <Module1 ref="module1" />
    <Module2 ref="module2" />
    <!-- 数据表格 -->
    <v-card>
      <v-card-title>
        <v-layout row wrap>
          <v-flex xs6 sm4 md2>
            <v-text-field v-model="doctor_no" label="医生账号"></v-text-field>
          </v-flex>
          <!-- <v-flex xs6 sm4 md2>
            <v-text-field v-model="out_trade_no" label="商户订单号"></v-text-field>
          </v-flex> -->
          <v-flex xs6 sm4 md2>
            <v-select
              @change="info(false)"
              menu-props="auto"
              v-model="status"
              :items="agent_list"
              item-text="text"
              item-value="value"
              return-object
              label="订单状态"
              required
            ></v-select>
          </v-flex>
          <v-btn :loading="loader" :disabled="loader" color="primary" @click="info(false)">筛选用户</v-btn>
        </v-layout>
        <b style="cursor:pointer" @click="h" >?</b>
      </v-card-title>
      <!--
        headers : 表头
        items： 表格内容
        total-items ： 总共的数据数量 （手动设置项目的总数，就可以禁用内置排序和分页）
        disable-initial-sort 在初始渲染时禁用默认排序
        :hide-actions="true" 隐藏表格操作
        :dark="true" 暗主题
        :loading="true" 加载进度条
      -->
      <v-data-table
        :headers="headers"
        :items="desserts"
        class="elevation-1"
        :total-items="allList"
        :rows-per-page-text="`每页行数`"
        :rows-per-page-items="pagingList"
        :disable-initial-sort="true"
        :loading="loader"
        @update:pagination="info"
      >
        <template v-slot:items="props">
          <tr @dblclick="info_data( props.item )">
          <td class="text-xs-centre">
            {{ props.item.doctor_phone }}
          </td>
          <td class="text-xs-centre">
            {{ props.item.doctor_name }}
          </td>
          <td class="text-xs-centre">
            {{ props.item.id }}
          </td>
          <td class="text-xs-centre">{{ (props.item.fee).toFixed(2) }} 元</td>
          <td class="text-xs-centre">
            {{ props.item.create_time | time }}
            <!-- <v-chip small v-for="(item,index) in props.item.agent_name" :key="index">{{ item }}</v-chip> -->
          </td>
          <td class="text-xs-centre">
            <v-img
            id="img___"
                :src="props.item.withdrawal_file"
                  lazy-src="data:image/jpeg;base64,"
                aspect-ratio="1"
                class="grey lighten-2"
                max-width="140"
                max-height="40"
                @click="img_(props.item.withdrawal_file)"
              ></v-img>
            <!-- <v-chip small v-for="(item,index) in props.item.agent_name" :key="index">{{ item }}</v-chip> -->
          </td>
          <td class="text-xs-centre">
            <!-- 
             状态:3020转入待入可提现账号,3021转入可提现账号,3022提现申请,3023提现处理中,3024提现成功,3025提现失败,3026提现失败退款,默认显示除了3020的所有状态
            -->
            <v-chip color="green" text-color="white" v-if="props.item.status == 3020">待入可提现账号</v-chip>
            <v-chip color="" v-if="props.item.status == 3021">转入医生账户</v-chip>
            <v-chip color="primary" text-color="white" v-if="props.item.status == 3022">提现申请</v-chip>
            <v-chip color="primary" text-color="white" v-if="props.item.status == 3023">提现处理中</v-chip>
             <v-chip color="primary" text-color="white" v-if="props.item.status == 3024">提现成功</v-chip>
            <v-chip color="red" text-color="white" v-if="props.item.status == 3025">提现失败</v-chip>
            <v-chip color="red" text-color="white" v-if="props.item.status == 3026">提现失败-金额退回医生账号</v-chip>
          </td>
          <td class="text-xs-centre">
              <span v-if="props.item.deal_time == 0" >未处理</span>
              <span v-else> {{ props.item.deal_time | time }} </span>
              </td>
          <td class="text-xs-centre">
            <span v-if="props.item.update_time == 0" >未完成</span>
            <span v-else >{{ props.item.update_time | time }}</span>
          </td>
          <td class="text-xs-centre">
            <v-chip color="green" text-color="white" v-if="props.item.status == 3020">待入可提现账号</v-chip>
             <span v-if="props.item.status == 3021">转入医生账户</span>
             <v-btn color="primary" dark v-if="props.item.status == 3022" @click="reviewIng( props.item.id )">审核订单</v-btn>
             <v-btn color="success" dark v-if="props.item.status == 3023" @click="reviewBox_S( props.item.id )" >提现成功</v-btn>
             <v-btn color="error" dark v-if="props.item.status == 3023" @click="reviewBox( props.item.id )" >提现失败</v-btn>
             <span v-if="props.item.status == 3024">提现成功</span>
             <span v-if="props.item.status == 3025">提现失败</span>
             <span v-if="props.item.status == 3026">提现失败-金额退回医生账号</span>
            <!-- <v-menu
              bottom
              origin="center center"
              :close-delay="100"
              transition="slide-x-transition"
            >
              <template v-slot:activator="{ on }">
                <v-btn color="primary" dark v-on="on">订单操作</v-btn>
              </template>
              <v-list>
                <v-list-tile  @click="info_data( props.item )">
                  <v-list-tile-title>订单详情</v-list-tile-title>
                </v-list-tile>
              </v-list>
            </v-menu> -->
          </td>
          </tr>
        </template>
        <!-- 当没有数据的时候，可以使用 no-data 插槽显示一段自定义HTML。 -->
        <template v-slot:no-data>
          <p class="p_text">没有更多数据了 :(</p>
        </template>
        <!-- 如果你还想添加一些额外的功能，比如每列过滤或者搜索，你可以使用 footer 插槽。 -->
        <!-- <template v-slot:footer>
          <td :colspan="headers.length">
            <strong>总计：</strong>
          </td>
        </template>-->
        <!-- 你可以使用page-text 插槽来自定义显示范围以及总条目。 -->
        <template
          v-slot:pageText="props"
        >当前 {{ props.pageStart }} - {{ props.pageStop }} 总共 {{ props.itemsLength }}</template>
      </v-data-table>
    </v-card>
    <!-- 确认框 -->
    <v-dialog v-model="dialog_logout" max-width="290">
      <v-card>
        <v-card-title class="headline">删除操作</v-card-title>
        <v-card-text>确认删除该用户吗？确认删除将不可恢复</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" flat="flat" @click="dialog_logout = false">取消</v-btn>
          <v-btn color="green darken-1" flat="flat" @click="delMessage">确认删除</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <!-- 提现失败操作备注 -->
    <v-layout row justify-center>
    <v-dialog v-model="dialog" persistent max-width="600px">
      <v-card>
        <v-card-title>
          <span class="headline"> &nbsp; &nbsp;操作备注信息</span>
        </v-card-title>
        <v-card-text>
          <v-container grid-list-md>
            <v-layout wrap>

              <v-flex xs12 sm12 md12>
                <!-- <v-text-field label=" 失败原因 " hint="" v-model="attach" ></v-text-field> -->
                <v-textarea
                    v-model="attach" 
                    outline
                    name="input-7-4"
                    label="输入描述"
                    value=""
                  ></v-textarea>
              </v-flex>

            </v-layout>
          </v-container>
          <small> &nbsp; &nbsp; &nbsp; &nbsp; *请填写具体失败的原因</small>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="red darken-1" flat @click="dialog = false"> 取消 </v-btn>
          <v-btn color="primary" dark @click="reviewOut"> 提交 </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-layout>
  <!-- 提现成功输入订单号 -->
    <v-layout row justify-center>
    <v-dialog v-model="dialog__" persistent max-width="600px">
      <v-card>
        <v-card-title>
          <span class="headline"> &nbsp; &nbsp;微信付款订单号</span>
        </v-card-title>
        <v-card-text>
          <v-container grid-list-md>
            <v-layout wrap>

              <v-flex xs12 sm12 md12>
                <v-text-field label=" 订单号 " hint="" v-model="trade_id" ></v-text-field>
              </v-flex>

            </v-layout>
          </v-container>
          <small> &nbsp; &nbsp; &nbsp; &nbsp; *请务必填写正确的微信付款订单号码（可在微信账单详情中找到交易单号）</small>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="red darken-1" flat @click="dialog__ = false"> 取消 </v-btn>
          <v-btn color="primary" dark @click="reviewSuccess"> 提交 </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-layout>
  <!-- 帮助 -->
  <div class="text-xs-center">
    <v-dialog
      v-model="dialog_h"
      width="500"
    >
      <v-card>
        <v-card-text>
          <p>待入可提现账号: 用户已购买付费咨询包，咨询次数还没用完，金额待入医生可提现账户。</p>
          <p>转入医生账户: 用户已购买付费咨询包，咨询次数已经用完，金额转入医生可提现账户。</p>
          <p>提现申请: 医生在客户端发起提现申请。</p>
          <p>提现处理中: 医生在客户端发起提现申请，后台管理人员正在处理当中。</p>
          <p>提现成功: 后台管理人员已转账成功，并提交了转账单号。</p>
          <p>提现失败: 后台管理人员转账失败，并提交的失败的原因（如：二维码模糊，无法扫码）</p>
          <p>提现失败-金额退回医生账号: 后台管理人员转账失败，医生发起提现的金额将退回到医生的可提现账户中。</p>
        </v-card-text>
        <v-divider></v-divider>
      </v-card>
    </v-dialog>
  </div>
  </div>
</template>
<script>
import Breadcrumbs from "../../../components/breadcrumbs/breadcrumbs.vue";
import Module1 from "./components/module1.vue";
import Module2 from "./components/module2.vue";
export default {
  components: {
    Breadcrumbs,
    Module1,
    Module2
  },
  data() {
    return {
      // 确认框
      dialog_logout: false,
      // 失败备注信息
      dialog: false,
      attach: "",
      // 帮助
      dialog_h: false,
      // 成功订单号
      dialog__: false,
      trade_id:"",
      id: 0,
      // 订单状态
      agent_list: [
        {
          text: "全部",
          value: 0
        },
        {
          text: "提现申请",
          value: 3022
        },
        {
          text: "提现处理中",
          value: 3023
        },
        {
          text: "提现成功",
          value: 3024
        },
        {
          text: "提现失败",
          value: 3025
        },
        {
          text: "转入医生账户",
          value: 3021
        },
        {
          text: "提现失败-金额退回医生账号",
          value: 3026
        }
      ],
      // 筛选数据
      user_id: 0,
      doctor_id: 0,
      status: {text: "全部",value: 0},
      page: 0,
      page_count: 10,
      out_trade_no: "",
      doctor_no: "",
      // 分页数据
      pagingList: [10, 20, 30, 40, 50, { text: "100", value: 100 }],
      loader: false,
      allList: 0,
      // 导航数据
      items: [
        {
          text: "首页",
          disabled: false,
          to: "/home"
        },
        {
          text: "提现订单列表",
          disabled: false,
          to: "/withdrawalOrder"
        }
      ],
      // 表格数据
      headers: [
        { text: "医生账号", value: "carbs", sortable: false },
        { text: "医生姓名", align: "left", sortable: false, value: "name" },
        { text: "订单ID", align: "left", sortable: false, value: "name" },
        { text: "提现金额", value: "calories", sortable: false },
        { text: "提现时间", value: "fat", sortable: false },
        { text: "收款二维码", value: "fat", sortable: false },
        { text: "提现状态", value: "protein", sortable: false },
        { text: "订单处理时间", value: "iron", sortable: false },
        { text: "订单完成时间", value: "iron", sortable: false },
        { text: "操作/说明", value: "iron", sortable: false }
      ],
      desserts: [],
      // 权限数据
      root_path: this.$cookie.getLocalStorage("root_path").root_path
    };
  },
  mounted: function() {
  },
  methods: {
    // 帮助
    h(){
      this.dialog_h = true;
    },
    // 输入订单号
    reviewBox_S( id ){
      this.id = id;
      this.dialog__ = true;
    },
    // 输入备注
    reviewBox(id){
      this.id = id;
      this.dialog = true;
    },
    // 成功操作
    reviewSuccess(){
      if( this.trade_id.length !== 28 ){
        this.$Dialog.getProductCheckResult_({text: "请务必填正确的28位付款订单号！",timeout: 3000, snackbar: true });
        return;
      }

      this.$axios({
        method: "post",
        url: this.$apis.URL.doctor_transfer_draw,
        data: {
          id: this.id,
          status: 2,
          trade_id: this.trade_id
        }
      })
        .then(response => {
          if (response.data.result === 0) {
            this.dialog = false
            this.$Dialog.getProductCheckResult_({text: "此订单已标记为提现成功！",timeout: 3000, snackbar: true });
            this.dialog__ = false;
            this.info( false );
          } else {
            this.$Dialog.getProductCheckResult_({text: response.data.message,timeout: 3000, snackbar: true });
          }
          this.loader = false;
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({text: error,timeout: 3000, snackbar: true });
        });
    },
    // 失败操作
    reviewOut(){
      if( this.attach == "" ){
        this.$Dialog.getProductCheckResult_({text: "请务必填写详细的失败原因！",timeout: 3000, snackbar: true });
        return;
      }
      this.$axios({
        method: "post",
        url: this.$apis.URL.doctor_transfer_draw,
        data: {
          id: this.id,
          status: 3,
          attach: this.attach
        }
      })
        .then(response => {
          if (response.data.result === 0) {
            this.dialog = false
            this.$Dialog.getProductCheckResult_({text: "此订单已标记为提现成功！",timeout: 3000, snackbar: true });
             this.dialog = false;
            this.info( false );
          } else {
            this.$Dialog.getProductCheckResult_({text: response.data.message,timeout: 3000, snackbar: true });
          }
          this.loader = false;
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({text: error,timeout: 3000, snackbar: true });
        });
    },
    // 审核中
    reviewIng( id ){
      this.$axios({
        method: "post",
        url: this.$apis.URL.doctor_transfer_draw,
        data: {
          id: id,
          status: 1
        }
      })
        .then(response => {
          if (response.data.result === 0) {
            this.$Dialog.getProductCheckResult_({text: "此订单已标记为审核中！",timeout: 3000, snackbar: true });
            this.info( false );
          } else {
            this.$Dialog.getProductCheckResult_({text: response.data.message,timeout: 3000, snackbar: true });
          }
          this.loader = false;
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({text: error,timeout: 3000, snackbar: true });
        });
    },
      /** 图片预览 */
    img_( img ){
      if( img == "" ){
        this.$Dialog.getProductCheckResult_({text:  "未找到图片！",timeout: 3000, snackbar: true });
        return;
      }
      window.console.log( document.body.clientHeight )
      window.open(img,'newwindow', `height=600, width=800, top=${ document.body.clientHeight/2 - 200 }, left=${ document.body.clientWidth/2 - 400 }, toolbar=no, menubar=no, scrollbars=no, resizable=no,location=no, status=no`);
    },
    // 订单详情
    info_data(list) {
      let datas = {
        state: true,
        list: list
      };
      this.$refs.module2.info(datas);
    },
    // 删除订单
    delMessage() {},
    /* 聊天记录 */
    // manages_list( manages ){
    //   console.log( manages )
    //   this.$router.push({
    //             path: `/chatList`,
    //             query:{
    //               data: manages
    //             }
    //           });
    // },
    // 用户列表
    info(event) {
      window.console.log(event);
      window.console.log(event);
      this.loader = true;
      this.$axios({
        method: "post",
        url: this.$apis.URL.doctor_transfer_list,
        data: {
          user_id: this.user_id,
          doctor_id: this.doctor_id,
          status: this.status.value,
          page: event == false ? this.page : event.page - 1,
          page_count: event == false ? this.page_count : event.rowsPerPage,
          out_trade_no: this.out_trade_no,
          doctor_no: this.doctor_no
        }
      })
        .then(response => {
          if (response.data.result === 0) {
            this.desserts = response.data.datas.list;
            this.allList = response.data.datas.total_number;
            window.console.log(this.desserts);
          } else {
            this.$Dialog.getProductCheckResult_({text: response.data.message,timeout: 3000, snackbar: true });
          }
          this.loader = false;
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({text: error,timeout: 3000, snackbar: true });
        });
    }
  },
  watch: {}
};
</script>

<style lang="less" scoped>
#img___{
    cursor: pointer;
}
.grey.lighten-2{
    border-radius: 3px;
    border: solid 2px #000;
}
.flex {
  padding-right: 10px;
  padding-left: 10px;
}
</style>


