<!--
 * @Description: 角色详情信息弹出框模块
 * @LastEditors: Ji Yinghao
 * @Date: 2019-05-05 13:42:39
 * @LastEditTime: 2019-10-09 18:28:38
 -->
<template>
  <div id="switch__">
    <!-- <v-btn color="primary" @click="dialog = true" >+ 创建子账号123 </v-btn> -->
    <v-layout row justify-center>
      <v-dialog v-model="dialog" max-width="800px">
        <!-- <template v-slot:activator="{ on }">
          <v-btn color="primary" flat dark v-on="on">+ 创建 {{ dialog }} </v-btn>
        </template>-->

        <v-card>
          <v-card-title>
            <span class="headline">订单详情</span>
          </v-card-title>
          <v-card-text>
            <!-- <p class="root_p">
              <v-img
                :src="data.head_url"
                lazy-src="https://picsum.photos/id/11/10/6"
                aspect-ratio="1"
                class="grey lighten-2"
                max-width="60"
                @click="img_(data.head_url)"
              ></v-img>
            </p>-->

            <p class="root_p">
              <span class="order_title">医生账号</span>
              <v-chip>{{ data.doctor_phone }}</v-chip>
            </p>

            <p class="root_p">
              <span class="order_title">医生姓名</span>
              <v-chip>{{ data.doctor_name }}</v-chip>
            </p>

            <p class="root_p">
              <span class="order_title">提现订单ID</span>
              <v-chip>{{ data.id }}</v-chip>
            </p>

            <p class="root_p">
              <span class="order_title">提现金额</span>
              <v-chip>{{ (data.fee).toFixed(2) }}</v-chip>
            </p>

            <p class="root_p">
              <span class="order_title">提现时间</span>
              <v-chip>{{ data.create_time | time }}</v-chip>
            </p>

            <p class="root_p">
              <span class="order_title">收款二维码</span>
              <v-tooltip top>
                <template v-slot:activator="{ on }">
                  <span color="primary" dark v-on="on">
                    <v-img
                      id="img___"
                      :src="data.withdrawal_file"
                      lazy-src="https://picsum.photos/id/11/10/6"
                      aspect-ratio="1"
                      class="grey lighten-2"
                      max-width="740"
                      max-height="100"
                      @click="img_(data.withdrawal_file)"
                    ></v-img>
                  </span>
                </template>
                <span>{{ `点击图片在弹出框中完成扫码操作！` }}</span>
              </v-tooltip>

              <!-- <v-chip>{{ data.create_time | time }}</v-chip> -->
            </p>

            <p class="root_p">
            <span class="order_title">提现状态</span>
            <v-chip color="green" text-color="white" v-if="data.status == 3020">待入可提现账号</v-chip>
            <v-chip color="" v-if="data.status == 3021">转入医生账户</v-chip>
            <v-chip color="primary" text-color="white" v-if="data.status == 3022">提现申请</v-chip>
            <v-chip color="primary" text-color="white" v-if="data.status == 3023">提现处理中</v-chip>
            <v-chip color="primary" text-color="white" v-if="data.status == 3024">提现成功</v-chip>
            <v-chip color="red" text-color="white" v-if="data.status == 3025">提现失败</v-chip>
            <v-chip color="red" text-color="white" v-if="data.status == 3026">提现失败-金额退回医生账号</v-chip>
            </p>

            <p class="root_p">
              <span class="order_title">订单处理时间</span>
              <v-chip>
                <span v-if="data.deal_time == 0">未处理</span>
                <span v-else>{{ data.deal_time | time }}</span>
              </v-chip>
            </p>

            <p class="root_p">
              <span class="order_title">订单完成时间</span>
              <v-chip v-if="data.update_time == 0">未完成</v-chip>
              <v-chip v-else>{{ data.update_time | time }}</v-chip>
            </p>

            <p class="root_p" v-if="data.transaction_id !== ''">
              <span class="order_title">微信交易订单号</span>
              <v-chip>{{ data.transaction_id }}</v-chip>
            </p>

            <!-- <p class="root_p" >已获得的权限</p>
                  <v-flex md12 sm12>
                   <v-chip class="text-xs-center" v-for="(item,index) in data.root_path" :key="index" >{{ item.name }}</v-chip> 
            </v-flex>-->
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <!-- <v-btn color="blue darken-1" flat @click="dialog = false">取消</v-btn> -->
            <v-btn color="info darken-1" @click="dialog = false">确认</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-layout>

  </div>
</template>

<script>
export default {
  data() {
    return {
      dialog: false,
      data: {
        attach: "",
        create_time: 0,
        deal_time: 0,
        fee: 0,
        new_amt: 0,
        old_amt: 0,
        status: 0,
        transaction_id: "",
        update_time: 0,
        withdrawal_file: ""
      }
    };
  },
  // 更新完成
  mounted: function() {},
  methods: {
    /** 初始化数据 */
    info(data) {
      this.dialog = data.state;
      this.data = data.list;
    },
    /** 图片预览 */
    img_(img) {
      if( img == '' ){ return };
      window.console.log(document.body.clientHeight);
      window.open(
        img,
        "newwindow",
        `height=600, width=800, top=${document.body.clientHeight / 2 - 200}, left=${document.body.clientWidth / 2 - 400}, toolbar=no, menubar=no, scrollbars=no, resizable=no,location=no, status=no`
      );
    }
  }
};
</script>

<style lang="less">

#switch__ {
  .v-input__slot {
    margin-bottom: 0px !important;
  }
  .v-input--selection-controls {
    margin-top: 0px !important;
  }
  .v-list__tile__content {
    display: block !important;
  }
  .v-list__tile--avatar {
    min-height: 70px;
    display: inline-table;
  }
}
.root_p {
  font-size: 16px;
  padding-left: 17px;
}
.order_title {
  display: inline-block;
  width: 125px;
}
</style>

